<template>
  <div class="accordion" id="accordionAssets">
    <asset
      v-for="(value, propertyName) in assets"
      v-bind:key="propertyName"
      :asset-name="propertyName"
      :asset-data="value"
    />
  </div>
</template>

<script>
import Asset from "./Asset.vue";

export default {
  name: "Assets",
  components: {
    Asset
  },
  computed: {
    assets() {
      return this.$store.state.Assets;
    }
  }
};
</script>
