<template>
  <div>
    <div class="accordion-item">
      <h2 class="accordion-header" :id="'heading' + assetId">
        <button
          class="accordion-button collapsed"
          type="button"
          data-bs-toggle="collapse"
          :data-bs-target="'#collapse' + assetId"
          aria-expanded="false"
          :aria-controls="'collapse' + assetId"
        >
          {{ heading }}
        </button>
      </h2>
      <div
        :id="'collapse' + assetId"
        class="accordion-collapse collapse"
        :aria-labelledby="'heading' + assetId"
        data-bs-parent="#accordionAssets"
      >
        <div class="accordion-body" style="text-align: left">
          
          <p v-if="this.$props.assetData['Description']" v-html="
                  require('markdown-it')().render(
                    this.$props.assetData['Description']
                  )
                ">
          </p>
          <ul v-if="this.$props.assetData['Abilities']">
            <li>
              <strong v-if="this.$props.assetData['Abilities'][0]['Name']">{{
                this.$props.assetData["Abilities"][0]["Name"]
              }}</strong>
              <p
                v-html="
                  require('markdown-it')().render(
                    this.$props.assetData['Abilities'][0]['Text']
                  )
                "
              ></p>
            </li>
            <li>
              <strong v-if="this.$props.assetData['Abilities'][1]['Name']">{{
                this.$props.assetData["Abilities"][1]["Name"]
              }}</strong>
              <p
                v-html="
                  require('markdown-it')().render(
                    this.$props.assetData['Abilities'][1]['Text']
                  )
                "
              ></p>
            </li>
            <li>
              <strong v-if="this.$props.assetData['Abilities'][2]['Name']">{{
                this.$props.assetData["Abilities"][2]["Name"]
              }}</strong>
              <p
                v-html="
                  require('markdown-it')().render(
                    this.$props.assetData['Abilities'][2]['Text']
                  )
                "
              ></p>
            </li>
          </ul>
          <p v-if="this.$props.assetData['Health']"><span style="font-weight: bold">Health:</span> {{this.$props.assetData['Health']}}</p>
          <span class="source">Source: {{ getSource() }}</span>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "Asset",
  props: {
    assetName: String,
    assetData: Object
  },
  computed: {
    heading() {
      return (
        this.$props.assetName + " (" + this.$props.assetData["Asset Type"] + ")"
      );
    },
    assetId() {
      return this.$props.assetName.replace(/\s+/g, "").replace(/[()]/g, "");
    }
  },
  methods: {
    getSource() {
      if(this.$props.assetData['Source']) {
        return this.$props.assetData['Source']['Name'];
      }

      return 'Ironsworn Assets';
    }
  }
};
</script>

<style scoped>
.source {
  color: #ccc;
  display: block;
  text-align: right;
  margin-top: 5px;
}
</style>